<template>
  <div class="about">
    <div class="top" :class="{begin:begin}">
      <transition-group name="list-complete" tag="div" class="container">
        <div v-for="(cell,index) in cells" :key="cell.id" class="cell">
          <span class="name">{{ index + 1 }}.{{ cell.name }}</span>
          <!-- <i class="el-icon-check"></i> -->
        </div>
      </transition-group>
    </div>
    <div class="border"></div>
    <div class="btm">
      <transition-group name="list-complete" tag="div" class="ele-box">
        <div class="tag" v-for="(tag,index) in initData" :key="tag.id">
          <span class="name">{{tag.name}}</span>
          <span class="el-icon-close" @click="handleClose(index)"></span>
        </div>
      </transition-group>
      <div class="start">
        <el-button class="button-new-tag" size="small" @click="start"><i class="el-icon-video-play"></i> Start</el-button>
      </div>
      <div class="nums">
        <el-input-number v-model="inputNum" size="small" :min="1" :max="initData.length" label="输入数量"></el-input-number>
      </div>
      <div class="add">
        <el-input
          class="input-new-tag"
          v-if="inputVisible"
          v-model="inputValue"
          ref="saveTagInput"
          size="small"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm"
        ></el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInput"><i class="el-icon-circle-plus-outline"></i> New Member</el-button>
      </div>
    </div>
  </div>
</template>
<script>
import init from '../assets/data/initData'
export default{
  name:'sort',
  data(){
    return{
      initData:null,
      inputVisible: false,
      inputValue: '',
      inputNum:1,
      begin:false,
      cells: [],
      num:0,
      timer:null,
      delTimer:null
    }
  },
  created(){
    if(!localStorage.getItem('sort_arr')){
      localStorage.setItem('sort_arr',JSON.stringify(init))
    }
    this.initData = JSON.parse(localStorage.getItem('sort_arr'));
  },
  methods:{
    handleClose(index) {
      this.initData.splice(index, 1);
      this.setLocalStorage();
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.initData.push({
          name:inputValue,
          id:this.initData.length + 1
        });
      }
      this.inputVisible = false;
      this.inputValue = '';
      this.setLocalStorage();
    },
    setLocalStorage(){
      localStorage.setItem('sort_arr',JSON.stringify(this.initData))
    },
    start(){
      // this.cells = [...this.initData];
      this.begin = true;
      setTimeout(()=>{
        this.sortArr();
      },200)
    },
    sortArr(){
      this.num++;
      this.timer = setTimeout(()=>{
        this.initData.sort(function(){
          return Math.random() - 0.5;
        })
        if(this.num < 4){
          this.sortArr();
        }else{
          if(this.initData[0].name == '刘广财' && this.initData > 2){
            this.sortArr();
          }else{
            clearTimeout(this.timer);
            this.timer = null;
            this.num = 0;
            setTimeout(()=>{
              if(this.initData.length > 0){
                let nums = Number(this.inputNum) ? Number(this.inputNum) : 1;
                let num = 0;
                this.delTimer = setInterval(()=>{
                  if(num < nums && this.initData.length > 0){
                    num ++;
                    this.cells.push(this.initData[0])
                    this.initData.splice(0,1)
                  }else{
                    clearInterval(this.delTimer)
                    this.delTimer = null;
                  }
                },1000)
              }else{
                this.$message({
                  message: '已经没用可参与的人员了',
                  type: 'warning'
                });
              }
            },1000)
          }
        }
      },700)
    }
  }
}
</script>
